import React from 'react';
import Chart from 'react-google-charts';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

export default class LineChart extends React.Component {
	constructor(props){
			super(props)
			//this.updateData = this.updateData.bind(this);
			this.state = {
					data : []
			}
	}

  componentDidUpdate(){
		console.log("hello");
	}
	render () {
		const { displayedItems } = this.props;

		const data = [
      ["ID", "Votes"]
    ];
		const isVoted = objectID => {
			const upVote = cookies.get("upvotes");
			if (!upVote) {
				//_voted = false;
				return false;
			}
			//console.log("upVote::"+upVote);
			let _voted =  (upVote.indexOf(objectID)) >= 0 ? true : false;
		//	console.log("_voted:::"+_voted);
			return _voted;
		}
    console.log("displayedItems::"+this.props.displayedItems.length);
		displayedItems && this.props.displayedItems.map(item => {
    const _voted = isVoted(item.objectID);
    const _score = parseInt(item.points);
		//_score=_voted ? _score + 1 : _score;
		// eslint-disable-next-line no-mixed-spaces-and-tabs
	  data.push([item.objectID ,_voted ? _score + 1 : _score]);
    });

    const options = {
      vAxis: {
        title: "Votes"
      },
      hAxis: {
        title: "ID"
      },
      legend: {
        position: 'none'
      }
    };
    console.log("data:"+data);
		return this.props.displayedItems.length <= 1 ? null : (
			<Chart
				chartType="LineChart"
				width="100%"
				height="400px"
				data={data}
				options={options}
			/>
		);

	}
}
